<template>
	<div class="supplier-accounts">
		<!--顶部菜单-->
		<nav-bar class="home-nav-bar">
			<div slot="left" class="icon-left" @click="backClick">
				<img src="~assets/images/common/prev2.svg">
			</div>
			<div slot="center">
				供应商对账
			</div>
			<div slot="right">
			</div>
		</nav-bar>
		<supplier-top-info :supplier="allCurrentObjs"/>
		<!--滚动--->
		<scroll class="supplier-scroller"
				ref="scroller"
				:probe-type=3
				@pullingup="loadMove"
		>
		<!--列表-->
		<supplier-accounts-content :balance-arr="supplierLists"/>
		</scroll>
	</div>
</template>

<script>

import NavBar from 'components/common/navbar/NavBar'
import Scroll from 'components/common/scroll/Scroll'

import SupplierAccountsContent from './childComps/SupplierAccountsContent'
import SupplierTopInfo from './childComps/SupplierTopInfo'
import {itemCustomerMixin} from 'common/mixin'	


export default {
  name: 'SupplierOfAccoun',
	components: {
		NavBar,
		Scroll,
		SupplierAccountsContent,
		SupplierTopInfo
	},
	methods:{
		backClick(){
			this.$router.go(-1)
		},
		loadMove(){
			
		},
	},
	mixins:[itemCustomerMixin],				//混入-获取客户列表
	data(){
		return{
			//欠款人和欠款总金额
			qiankuanObj:{
				allCustomer:0,
				allMoney:0
			},
			//设置值
			setValue(key,value){
				this.qiankuanObj[key]=value
			},
			//获取欠款客户的总金额或者总人数
			getAllAttrNum(arr,attr){
				if(arr.length<=0)return 0
				let res=0
				arr.forEach(obj=>{
					res+=parseFloat(obj[attr])
				})
				return res.toFixed(2)
			}
		}
	},
	computed:{
		//算出欠款总金额(返回值：对象)
		allCurrentObjs(){
			let allMoney=this.getAllAttrNum(this.customerList.supplier.list,'sex')	//获取
			this.setValue('allMoney',allMoney)							//设置
			this.setValue('allCustomer',this.customerList.supplier.list.length)	//设置
			return this.qiankuanObj
		},
		supplierLists(){
			let arr= this.customerList.supplier.list
			return  arr.sort(this.compare("documentnum"))			//排序
		}
	}
}
//高度问题来自页面高度不一致-上一个页面改了他的高度
</script >

